* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: linear-gradient(to left, rgba(138, 57, 63, .7), rgba(197, 149, 82, .7), rgba(169, 207, 127, .7), rgba(45, 107, 103, .7), rgba(85, 98, 112, .7));
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

p {
  margin: 0;
}

.search-form {
  justify-content: center;
  padding: 20px;
  display: flex;
}

.search-form input {
  min-width: 500px;
  height: 40px;
  border: 1px solid #4169e1;
  border-radius: 5px;
  outline: 1px solid #4169e1;
  margin-right: 15px;
  font-size: 18px;
  transition: outline .2s linear;
}

.search-form input:focus {
  outline: 1px solid #000;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4169e1;
  border: none;
  border-radius: 5px;
  transition: background-color .2s linear;
}

button:hover {
  background-color: #5fc0e6;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: baseline;
  gap: 30px;
  padding-top: 50px;
  padding-bottom: 50px;
  list-style: none;
  display: flex;
}

.photo-card {
  cursor: pointer;
  flex-basis: calc(20% - 6px);
  transition: scale .2s linear;
}

.photo-card img {
  border-radius: 15px 15px 0 0;
}

.photo-card:hover {
  scale: 1.03;
}

.info {
  text-align: center;
  background-color: rgba(65, 105, 225, .21);
  border: 1px solid gray;
  border-top: none;
  border-radius: 0 0 5px 5px;
  gap: 10px;
  padding: 5px;
  display: flex;
}

.info-item {
  font-size: 14px;
}

.info-item:not(:last-child) {
  border-right: 1px solid #4169e1;
  padding-right: 5px;
}

